import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import 'bootstrap/dist/css/bootstrap.min.css';

import { BrowserRouter } from 'react-router-dom';

import { Provider } from 'react-redux';
import { combineReducers, createStore } from 'redux';

let alertInit = true;

function reducer2(state = alertInit,action){

  if(action.type === false){
    state = false;
  }

  return state;
}

let InitData =  [
  {id : 0, name : 'nice', quan : 0},
  {id : 1, name : 'good', quan : 3}
];



function reducer(state = InitData, action){
  let index = action.index;
  
  if(action.type === 'itemAdd'){
    let found =state.findIndex((i)=>{ return i.id === action.payload.id });

    if(found >= 0 ){
      let changeState= [...state];
      changeState[found].quan++;
      return changeState;
    }
    else
    {  
      let changeState= [...state];
      changeState.push(action.payload);
      
      return changeState;
    }
  }
  else if(action.type === 'quanIncrease'){
    let changeState = [...state];
    changeState[index].quan++;
    return changeState;
  }else if(action.type === 'quanDecrease'){
    let changeState = [...state];
    changeState[index].quan--;
    changeState[index].quan = changeState[index].quan < 0 ? 0 : changeState[index].quan;

    return changeState;  
  }
  else{
    return state;
  }
}

const store = createStore(combineReducers({reducer,reducer2}));

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

reportWebVitals();
