/* eslint-disable */
// Library Module
import React,{ useState,lazy, Suspense } from 'react';
import { Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { Link, Route, Switch } from 'react-router-dom';
import axios from 'axios';
// Components
import Main from './components/Main/Main';
// import Detail from './components/Detail/Detail';
// import Cart from './components/Cart/Cart';
import data from './data.js';
// import LoadingBar from './components/Common/LoadingBar';
// CSS
import './App.css';
// Lazy Loading
let Detail = lazy(()=>{ return import('./components/Detail/Detail') });
let Cart = lazy(()=>{return import('./components/Cart/Cart') });
let LoadingBar = lazy(()=>{return import('./components/Common/LoadingBar') });

export const stockContext = React.createContext();

function App() {
  
  let [ shoes,setShoes ] = useState(data);
  let [ stock,setStock ] = useState([10,11,12]);
  let [ loading,setLoading ] = useState(false);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
      <Navbar.Brand href="/">Information ShoesTube</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="ml-auto">
          <Nav.Link as={Link} to="/">Home</Nav.Link>
          <Nav.Link as={Link} to={`/detail`}>Detail</Nav.Link>
          <NavDropdown title="Dropdown" id="basic-nav-dropdown">
            <NavDropdown.Item href="/Cart">장바구니</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
            <NavDropdown.Divider />
            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
          </NavDropdown>
        </Nav>
      </Navbar.Collapse>
      </Navbar>

      <Switch>
      {/* MainHome  */}
      <Route exact path="/">
          <stockContext.Provider value = { stock }>
            <Main shoes = { shoes } />
          </stockContext.Provider>
        <button className="btn btn-primary moreBtn" onClick={()=>{

          setLoading(true);
          {
            loading === true ?
            <LoadingBar /> : null
          }
          axios.get('https://codingapple1.github.io/shop/data2.json')
          .then((data)=>{
            setLoading(false);
            setShoes([...shoes, ...data.data]);

          })
          .catch(()=>{
            setLoading(false);
              console.log('실패했어요.');
          });
                  
        }}>더보기</button>
      </Route>

      {/* Detail */}
      <Route path="/detail/:id">
        <stockContext.Provider value = { stock }>
          <Suspense fallback={<div>로딩중이에요</div>}>
            <Detail data = {shoes} />
          </Suspense>
        </stockContext.Provider>
      </Route>

      <Route path="/Cart">
        <stockContext.Provider value = { stock }>
          <Suspense fallback={<div>로딩중이에요</div>}>
            <Cart />
          </Suspense>
        </stockContext.Provider>
      </Route>

      </Switch>

    </div>
  );
}

export default App;
